<template>
    <div>
        <div class="row justify-content-left m-2">
            <div class="col-md-12">
                <div class="card">
                    <div class="card-header">Edit Round</div>
                    <div class="card-body">
                        <form
                            @submit.prevent="fillForm"
                            enctype="multipart/form-data"
                        >
                            <div class="form-group row">
                                <label
                                    for="name"
                                    class="col-md-2 col-form-label text-md-right"
                                    >Name</label
                                >
                                <div class="col-md-9">
                                    <input
                                        id="name"
                                        type="text"
                                        class="form-control"
                                        name="name"
                                        required
                                        autocomplete="name"
                                        autofocus
                                        v-model="name"
                                    />
                                </div>
                            </div>

                            <!-- <div class="form-group row">
                                <label
                                    for="name"
                                    class="col-md-2 col-form-label text-md-right"
                                    >Teams</label
                                >
                                <div class="col-md-9">
                                    <Multiselect
                                        v-model="value"
                                        mode="multiple"
                                        placeholder="Select your characters"
                                        :close-on-select="false"
                                        :options="options"
                                    />
                                </div>
                            </div> -->

                            <!-- start of error section -->
                            <div
                                class="card w-75 mx-auto mb-3 alert alert-danger"
                                v-if="errors"
                            >
                                <ul class="list-group list-group-flush">
                                    <li
                                        class="list-group-item"
                                        v-if="errors.name"
                                    >
                                        <div
                                            v-for="error in errors.name"
                                            :key="error"
                                        >
                                            {{ error }}
                                        </div>
                                    </li>
                                    <li
                                        class="list-group-item"
                                        v-if="errors.team_ids"
                                    >
                                        <div
                                            v-for="error in errors.team_ids"
                                            :key="error"
                                        >
                                            {{ error }}
                                        </div>
                                    </li>
                                </ul>
                            </div>
                            <!-- end of error section -->

                            <div class="form-group row mb-0 text-right">
                                <div class="col-md-6 offset-md-4">
                                    <button
                                        type="submit"
                                        class="btn btn-primary"
                                        :disabled="isLoading || sameValue"
                                    >
                                        Submit
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Multiselect from "@vueform/multiselect";

export default {
    components: {
        Multiselect,
    },

    props: ["isLoading", "errors", "oldName", "oldTeams", "teams"],

    emits: ["submitRound"],

    data() {
        return {
            name: this.oldName,
            options: [],
            value: null,
            image: null,
            sameValue: true,
        };
    }, //end of data

    watch: {
        name(newName) {
            if (newName == this.oldName) {
                this.sameValue = true;
            } else {
                this.sameValue = false;
            }
        }, //end of name

        value(newValue) {
            if (newValue == null) {
                this.sameValue = true;
            } else {
                this.sameValue = false;
            }
        }, //end of value

        image(newImage) {
            if (newImage == null) {
                this.sameValue = true;
            } else {
                this.sameValue = false;
            }
        }, //end of password
    }, //end of watch

    methods: {
        uploadImage(event) {
            this.image = event.currentTarget.files[0];
        }, //end of image upload

        fillForm() {
            let formData = new FormData();
            formData.append("name", this.name);
            if (this.value) {
                formData.append("team_ids", JSON.stringify(this.value));
            }
            formData.append("_method", "PUT");
            this.$emit("submitRound", formData);
        }, //end of filling form
    }, //end of methods

    mounted() {
        this.options = this.teams.map((team) => {
            return {
                value: team.id,
                label: team.name,
            };
        });

        this.value = this.oldTeams.map((team) => {
            return team.id;
        });
    }, //end of mounted
};
</script>
